details {
  width: 300px;
  margin-right: 1rem;
  position: relative;
}

details[open] {
  z-index: 1;
}

summary {
  width: 93px;
  height: 34px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #fd5103;
  border-radius: 4px;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  list-style: none;
  display: flex;
}

summary::-webkit-details-marker {
  display: none;
}

details[open] summary:before {
  content: "";
  width: 100vw;
  height: 100vh;
  background: none;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

summary:after {
  content: "";
  width: 8px;
  height: 8px;
  border-bottom: 2px solid #fd5103;
  border-left: 2px solid #fd5103;
  border-bottom-left-radius: 2px;
  margin: 2px 8px 8px 6px;
  transition: transform .1s ease-in-out;
  display: block;
  transform: rotate(45deg)translate(50%);
}

summary:focus {
  outline: none;
}

details[open] summary:after {
  transform: rotate(-45deg)translate(0%);
}

.select-item {
  margin: 0;
  padding: 10px;
}

.select-item:hover {
  background-color: rgba(253, 82, 3, .184);
}

summary.radios {
  counter-reset: radios;
}

summary.radios:before {
  content: var(--selection);
}

input[type="radio"] {
  counter-increment: radios;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  display: none;
}

input[type="radio"]:checked {
  --display: block;
  display: inline;
}

input[type="radio"]:after {
  content: attr(title);
  color: #fd5103;
  padding-left: 7px;
  font-size: 16px;
  display: block;
}

.select-list {
  counter-reset: labels;
  width: 93px;
  height: 84px;
  max-height: 200px;
  background: #fff;
  border: 1px solid #fd5103;
  border-radius: 4px;
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  top: 100%;
  left: 0;
  overflow-y: auto;
}

label {
  width: 100%;
  cursor: pointer;
  color: #5f6775;
  justify-content: space-between;
  font-size: 10px;
  display: flex;
}

.select-list::-webkit-scrollbar {
  width: 4px;
  background: rgba(253, 82, 3, .13);
}

.select-list::-webkit-scrollbar-thumb {
  background: #fd5103;
  border-radius: 10px;
}

.select-list::-webkit-scrollbar-thumb:hover {
  background: #b30000;
}

